@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@400;500;600;700&display=swap);
:root {
  --color1: #171941;
  --color2: #222222;
  --color3: #333333;
  --color4: #40a1f0;
  --color5: #ffffff;
  --color6: #D5D5D5;
  --color7: #dfdfdf;
}

body {
  font: 400 14px 'poppins';
  background-color: #222222;
  background-color: var(--color2);
  color: #D5D5D5;
  color: var(--color6);
}

.nav-link {
  color: #ffffff;
  color: var(--color5);
  transition: 0.3s;
}

.nav-link:hover {
  color: #dfdfdf;
  color: var(--color7);
}

.navbar {
  transition: 0.5s;
}

.navbar-nav li {
  padding: 0 10px;
}

.navbar.navbar-transparent {
  padding-top: 25px;
}

.bg-info {
  background-color: #40a1f0 !important;
  background-color: var(--color4) !important;
}

.navbar-brand {
  color: #ffffff;
  color: var(--color5);
  transition: 0.3s;
}

.navbar-brand:hover {
  color: #dfdfdf;
  color: var(--color7);
}

h1 {
  font: 400 32px "Poppins";
}

.navbar-translate {
  width: 100%;
  position: relative;
  display: flex;
  justify-content: space-between!important;
}

.navbar-translate button {
  color: #ffffff;
  color: var(--color5);
  border: 0px solid #ffffff;
  border: 0px solid var(--color5);
  outline:none;
}

.container-footer {
  padding-top: 10vh;
}

@media (max-width: 700px) {
  .container-footer .col {
    text-align: center;
  }
}
.container-home {
    padding-top: 25vh;
}

.card-home {
    background: transparent;
    border: 0px solid #fff;
}

.img-home {
    max-width: 300px;
    width: 100%;
}

.home-description {
    font: 400 14px "Poppins";
    color: #ffffffcc;
}
.container-skills {
  padding-top: 20vh;
}

.container-skills .row{
  padding-top: 15px;
  padding-bottom: 15px;
}

.container-skills span{
  font: 400 33px "Poppins";
}

.card-skills {
  background-color: var(--color3);
  border-color: var(--color3);

  margin-top: 10px;
  margin-bottom: 10px;
}
.container-projects {
  padding-top: 25vh;
}

.projects-card {
  background-color: var(--color3);
}
.container-projects-fun {
  padding-top: 25vh;
}

.projects-fun-card {
  background-color: var(--color3);
}
